<template>
  <!-- vditorvditor#vditor -->
  <div id="vditor">
    <!-- vditorloading -->
    <t-skeleton
      class="vditor-skeleton"
      :rowCol="skeletonRowCol"
      loading
      animation="gradient"
    ></t-skeleton>
  </div>
</template>

<script>
import Vditor from 'vditor';
import { vditorBaseConfigs, skeletonRowCol } from '@/views/article/constants';
import 'vditor/dist/index.css';

export default {
  props: {
    value: {
      type: String,
      require: false,
      default: '',
    },
  },
  computed: {
    isDark() {
      return this.$store.state.app.isDark;
    },
  },
  watch: {
    //  store
    isDark(isDark) {
      isDark
        ? this.contentEditor?.setTheme('dark', 'dark')
        : this.contentEditor?.setTheme('classic', 'light');
    },
  },
  data() {
    return {
      contentEditor: null,
      delayTime: 500,
      skeletonRowCol: skeletonRowCol,
    };
  },
  methods: {
    contentChange() {
      this.$emit('input', this.contentEditor.getValue());
    },
    countWord(length) {
      this.$emit('countWord', length);
    },
    append(content) {
      this.contentEditor.insertValue(content);
    },
    initViditor(cb) {
      const { isDark, value } = this;
      const theme = isDark ? 'dark' : 'classic';
      // 500msvditor
      setTimeout(() => {
        this.contentEditor = new Vditor('vditor', {
          ...vditorBaseConfigs,
          input: this.contentChange,
          theme,
          preview: {
            theme: {
              current: theme
            }
          },
          value,
          counter: {
            enable: true,
            type: 'text',
            after: (length) => {
              this.countWord(length);
              cb();
            },
          },
        });
      }, this.delayTime);
    },
  },
};
</script>

<style lang="less" scoped>
.vditor-skeleton {
  padding: 20px;
  height: 600px;
}
</style>
